@media screen and (min-width: 1000px) {
  section .row {
    background-color: #832037;
    border-radius: 20px;
  }
  section .row h2, section .row p {
    color: white;
    text-align: center;
  }
}
@media screen and (max-width: 1000px) {
  section .row {
    background-color: #832037;
    border-radius: 20px;
  }
  section .row h2, section .row p {
    color: white;
    text-align: center;
  }
  section .row h2 {
    font-size: 38px;
  }
  section .row p {
    font-size: 30px;
  }
