@media screen and (min-width:1000px){
    .fD{
        border-radius: 20px;
        border: 0.1px solid black;
        .imm{
            border-radius: 20px;
        }
    }
    h2{
        color: #480a18;
        font-size: 50px;
        line-height: 60px;
    }
    p{
        color: #777;
        margin: 15px 0px;
    }
    button{
        width: 150px;
        height: 40px;
        margin: 20px 10px 20px 0px;
    }
    .d5{
        width: 60%;
        height: auto;
        opacity: 1;
        position: relative;
        left: 20%;
        color: #777;
    }
    .c2{
        .im{
            border-radius: 20px;
            width: 100%;
            height: 100%;
        }
    }
    .c3d{
        background-color: #71152b;
        border-radius: 20px;
        margin-right: auto;
        h4{
            text-align: center;
            color: white;
        }
        .u1{
            list-style: none;
            .dd1{
                background-color: white;
                border-radius: 50px;
                .pp1{
                    color: black;
                    text-align: center;
                    font-weight: bold;
                }
            } 
        }
    }
}
@media screen and (max-width:1000px){
    .fD{
        border-radius: 20px;
        border: 0.1px solid black;
        .imm{
            border-radius: 20px;
        }
    }
    h2{
        color: #480a18;
        font-size: 42px;
    }
    p{
        color: #777;
        margin: 15px 0px;
        font-size: 35px;
    }
    button{
        width: 200px;
        height: 86px;
        margin: 20px 10px 20px 0px;
        font-size: 28px;
    }
    .d5{
        width: 60%;
        height: auto;
        opacity: 1;
        position: relative;
        left: 20%;
        color: #777;
        font-size: 35px;
    }
    .c2{
        .im{
            border-radius: 20px;
            width: 100%;
            height: 100%;
        }
    }
    .c3d{
        background-color: #71152b;
        border-radius: 20px;
        margin-bottom: 5px;
        h4{
            text-align: center;
            color: white;
            font-size: 38px;
        }
        .u1{
            list-style: none;
            .dd1{
                background-color: white;
                border-radius: 50px;
                .pp1{
                    color: black;
                    text-align: center;
                    font-weight: bold;
                    font-size: 32px;
                }
            } 
        }
    }
}
