@media screen and (min-width:1000px){
    section{
        .row{
            background-color: #832037;
            border-radius: 20px;
            h2, p{
                color: white;
                text-align: center;
            }
        }
    }
}
@media screen and (max-width:1000px){
    section{
        .row{
            background-color: #832037;
            border-radius: 20px;
            h2, p{
                color: white;
                text-align: center;
            }
            h2{
                font-size: 38px;
            }
            p{
                font-size: 30px;
            }
        }
    }
}
