*{
    font-family: 'Lora', serif;
    font-family: 'Markazi Text', serif;
    font-family: 'Playfair Display', serif;
}
@media screen and (min-width:1000px){
    .d1{
        width: 100%;
        height: 90px;
        position: relative;
        background-color: #832037;
        nav{
            background-color: white;
            width: 99%;
            height: 88%;
            position: absolute;
            left: 0.5%;
            top: 6%;
            border-radius: 20px;
            .navbar-nav{
                position: absolute;
                left: 10%;
                top: 22%;
                .nav-link{
                    color: black;
                }
            }
        }
        .im{
            position: absolute;
            right: 20%;
            top: 15%;
            width: 70px;
            height: 70px;
        }
    }
    header{
        h2{
            color: #480a18;
            font-size: 50px;
            line-height: 60px;
        }
        p{
            color: #777;
            margin: 15px 0px;
        }
        button{
            width: 150px;
            height: 40px;
            margin: 20px 10px 20px 0px;
        }
        .fm{
            width: 60px;
            height: 60px;
        }
        .secD{
            border-radius: 20px;
            .imm{
                border-radius: 20px;
            }
        }
    }
}
@media screen and (max-width:1000px){
    .d1{
        width: 100%;
        height: 120px;
        position: relative;
        background-color: #832037;
        nav{
            background-color: white;
            width: 99%;
            height: 88%;
            position: absolute;
            left: 0.5%;
            top: 6%;
            border-radius: 20px;
            .navbar-nav{
                position: absolute;
                left: 10%;
                top: 22%;
                .nav-link{
                    color: black;
                    h4{
                        font-size: 35px;
                        margin-left: 10px;
                    }
                }
            }
        }
        .im{
            position: absolute;
            right: 8%;
            top: 10%;
            width: 100px;
            height: 100px;
        }
    }
    header{
        h2{
            color: #480a18;
            font-size: 42px;
        }
        p{
            color: #777;
            font-size: 35px;
            margin: 15px 0px;
        }
        button{
            width: 200px;
            height: 86px;
            margin: 20px 10px 20px 0px;
            font-size: 28px;
        }
        .fm{
            width: 100px;
            height: 100px;
        }
        .secD{
            border-radius: 20px;
            .imm{
                border-radius: 20px;
            }
        }
    }
}
